












import { Component, Prop, Vue } from 'vue-property-decorator';

@Component<SearchBar>({
    watch: {
        keywords: function(newVal: string): void {
            this.$emit("input", newVal);
        }
    }
})
export default class SearchBar extends Vue {
    //@ts-ignore
    @Prop() value: string;
    private keywords: string = "";
    private open = false;
}
